import React from 'react'
import classes from './ProfileInfo.module.css'
import defaultAvatar from '../../../assets/defaultAvatar.png'
import ProfileStatusWithHooks from './ProfileStatus/ProfileStatusWithHooks'
import cn from 'classnames'
import { profileType } from '../../../types/types'

type profileInfoPropsType = {
  randomUserId: number
  loggedInUserId: number
  status: string | undefined
  profile: profileType
  loadingPhoto: boolean
  upDateStatus: () => void
  upDatePhoto: (arg: any) => void
  getNewUserPhotoFromServer: (loggedInUserId: number) => void
  loadingPhotoThunk: () => void
}

const ProfileInfo: React.FC<profileInfoPropsType> = (props) => {

  const {
    randomUserId,
    loggedInUserId,
    loadingPhotoThunk,
    loadingPhoto,
    profile,
    status,
    upDateStatus,
  } = props

  const {
    facebook,
    website,
    vk,
    twitter,
    instagram,
    youtube,
    github,
    mainLink
  } = profile.contacts

  const changeAvartarPhoto = (event: HTMLElement) => {
    //@ts-ignore
    if (event.target.files.length) {
      loadingPhotoThunk()
    //@ts-ignore
      async function uploadUserPhoto() {
    //@ts-ignore
        await props.upDatePhoto(event.target.files[0])
        props.getNewUserPhotoFromServer(props.loggedInUserId)
        loadingPhotoThunk()
      }
      return uploadUserPhoto()
    }
  }

  let buttonContainer = document.getElementById('button-container')

  const showButton = () => {
    if (randomUserId === loggedInUserId) {
      if (buttonContainer) buttonContainer.style.display = 'block'
    }
  }

  const hideButton = () => {
    if (randomUserId === loggedInUserId) {
      if (buttonContainer) buttonContainer.style.display = 'none'
    }
  }

  let modal = document.getElementById('modal')

  const showModal = () => {
    if (profile.photos.large) {
      if (modal) modal.style.display = 'flex'
    }
  }

  const hideModal = () => {
    if (modal) modal.style.display = 'none'
  }



  return (
    <div className={classes.profileInfo}>
      <h1>{profile.fullName}</h1>
      <div>
        <ProfileStatusWithHooks
          upDateStatus={upDateStatus}
          status={status}
          randomUserId={randomUserId}
          loggedInUserId={loggedInUserId}
        />
      </div>
      <div>
        <span><b>Обо мне:</b> {profile.lookingForAJobDescription}</span>
        <p></p>
      </div>
      <div className={classes.profileInfoContainer}>

        <div
          onMouseOver={showButton}
          onMouseOut={hideButton}
          className={classes.profilePictureContainer}
        >

          <img onClick={showModal} alt='profilePic' src={profile.photos.large || defaultAvatar} />
          <div id='button-container' className={classes.addPhotoButtonContainer}>

            <input type={'file'}
              //@ts-ignore
              onChange={changeAvartarPhoto} />
          </div>
          <div className={cn(classes.loadingPhotoContainer, {
            [classes.hideLoading]: !loadingPhoto
          })}>
            <div>
              Loading photo...
            </div>
          </div>
        </div>

        <div className={classes.contacts}>
          <ul>
            <b>Контакты:</b>
            <li>facebook: <span><a href={facebook}>{facebook}</a></span></li>
            <li>website: <span><a href={website}>{website}</a></span></li>
            <li>vk: <span><a href={vk}>{vk}</a></span></li>
            <li>twitter: <span><a href={twitter}>{twitter}</a></span></li>
            <li>instagram: <span><a href={instagram}>{instagram}</a></span></li>
            <li>youtube: <span><a href={youtube}>{youtube}</a></span></li>
            <li>github : <span><a href={github}>{github}</a></span></li>
            <li>mainilnk: <span><a href={mainLink}>{mainLink}</a></span></li>
          </ul>
        </div>
      </div>
      <div>
        <ul>
          <li>
            <b>Ищу работу:</b> {profile.lookingForAJob == true ? <span>Да</span> : <span>Нет</span>}
          </li>
          <li>
            <b>Какую работу ищу:</b> <span>{profile.lookingForAJobDescription}</span>
          </li>
        </ul>
      </div>
      <hr />

      <div id='modal' className={classes.modal}>
        <div className={classes.modalContainer}>
          <img
            //@ts-ignore
            src={profile.photos.large}
            alt="profile photo large" />
        </div>
        <div className={classes.hideModalButton}>
          <button onClick={hideModal}>close</button>
        </div>
      </div>
    </div>
  )
}
export default ProfileInfo