import React from 'react';
import { postsType, profileType } from '../../types/types';
import Posts from './Posts/Posts';
import ProfileInfo from "./ProfileInfo/ProfileInfoTS";

type profilePropsType = {
    status: string | undefined
    profile: profileType
    posts: Array<postsType>
    userId: number
    upDateStatus: () => void
    updatePhoto: () => void
    getUserProfilePhoto: () => void
    loadingPhotoThunk: () => void
    loadingPhoto: boolean
}

const Profile : React.FC<profilePropsType> = (props) => {
    return (
        <div>
            <ProfileInfo 
            randomUserId={props.profile.userId}
            loggedInUserId={props.userId}
            status={props.status} 
            profile={props.profile}
            upDateStatus={props.upDateStatus}
            upDatePhoto={props.updatePhoto}
            getNewUserPhotoFromServer={props.getUserProfilePhoto}
            loadingPhotoThunk={props.loadingPhotoThunk}
            loadingPhoto={props.loadingPhoto}
            />
            {props.userId === props.profile.userId ?  <Posts posts={props.posts}/> : null}
           
        </div>
    )
}

export default Profile;