import React, { useEffect, useState } from 'react'
import classes from './ProfileStatus.module.css'

const ProfileStatusWithHooks = (props) => {
    let [editMode, setEditMode] = useState(false)
    let [status, setStatus] = useState(props.status)

    useEffect( () => {
        setStatus(props.status)
    }, [props.status] )

    const activateStatusChange = () => {
        if (props.loggedInUserId === props.randomUserId) {
            setEditMode(true)
        }
        else {
            return null
        }
    }
    const deactivateStatusChange = () => {
        setEditMode(false)
        props.upDateStatus(status)
    }
    const onStatusChange = (elem) => {
        setStatus(elem.currentTarget.value)
    }
    const showStatusChange = () => {
        if (props.loggedInUserId === props.randomUserId) {
            let statusChangeSpan = document.getElementById('statusChange')
            statusChangeSpan.style.display = 'block'
        }
    }
    const hideStatusChange = () => {
        let statusChangeSpan = document.getElementById('statusChange')
        statusChangeSpan.style.display = 'none'
    }
    return (
        <>
            {!editMode ?
                <div id='status-container' onMouseLeave={hideStatusChange} className={classes.status}>
                    <div onMouseEnter={showStatusChange}>
                    <span onClick={activateStatusChange}><b>Статус: </b> {props.status}</span>
                    </div>
                    <div id='statusChange' className={classes.changeStatus}>
                        <span onClick={activateStatusChange}>change status</span>
                    </div>
                    <p></p>
                </div>
                : <div>
                    <input
                        autoFocus={true}
                        onBlur={deactivateStatusChange}
                        onChange={onStatusChange}
                        type="text"
                        value={status} />
                    <p></p>
                </div>
            }
        </>
    )
}

export default ProfileStatusWithHooks

