import React from 'react';
import Profile from './ProfileTS';
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
    getUserProfileThunkCreator,
    removeUserProfileThunkCreator,
    getUserStatusThunkCreator,
    updateUserStatusThunkCreator,
    uploadUserProfilePhotoThunkCreator,
    loadingPhotoThunk
} from "../../OldReducers/profileReducer";
import { getUserProfilePhotoThunk } from '../../OldReducers/headerRecuder'
import Preloader from '../Common/Preloader/Preloader';
import { LoggedInRedirect } from '../../hoc/LoggedInRedirect';
import { compose } from 'redux';
import { AppStateType } from '../../redux/reduxStoreTS';
import { postsType, profileType } from '../../types/types';

type profileContainerPropsType = {
    profile: profileType
    posts: Array<postsType>
    status: string | undefined
    loadingPhoto: boolean
    userId: number
    getUserProfileThunkCreator: (userId: number) => void
    getUserStatusThunkCreator: (userId: number) => void
    removeUserProfileThunkCreator: () => void
    updateUserStatusThunkCreator: () => void
    uploadUserProfilePhotoThunkCreator: () => void
    getUserProfilePhotoThunk: () => void
    loadingPhotoThunk: () => void
    match: any // Выяснить что за match
}

class ProfileInnerContainer extends React.Component<profileContainerPropsType> {

    componentDidMount() {
        
        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.userId;
        }
        this.props.getUserProfileThunkCreator(userId)
        this.props.getUserStatusThunkCreator(userId)
    }
    componentWillUnmount() {
        this.props.removeUserProfileThunkCreator()
    }
    render() {
        if (!this.props.profile) {
            return <Preloader />
        }
        return (
            <Profile
                status={this.props.status}
                profile={this.props.profile}
                posts={this.props.posts}
                upDateStatus={this.props.updateUserStatusThunkCreator}
                userId={this.props.userId}
                updatePhoto={this.props.uploadUserProfilePhotoThunkCreator}
                getUserProfilePhoto={this.props.getUserProfilePhotoThunk}
                loadingPhotoThunk={this.props.loadingPhotoThunk}
                loadingPhoto={this.props.loadingPhoto}
            />
        )
    }
}

let mapStateToProps = (state : AppStateType) => ({
    profile: state.profileReducer.profile,
    posts: state.profileReducer.posts,
    status: state.profileReducer.userStatus,
    loadingPhoto: state.profileReducer.loadingPhoto,
    userId: state.authReducer.userId,
})

export default compose(
    withRouter,
    connect(mapStateToProps, {
        getUserProfileThunkCreator,
        removeUserProfileThunkCreator,
        getUserStatusThunkCreator,
        updateUserStatusThunkCreator,
        uploadUserProfilePhotoThunkCreator,
        getUserProfilePhotoThunk,
        loadingPhotoThunk
    }),
    LoggedInRedirect,
)(ProfileInnerContainer)